h\n    }\n\n    .react-aria-Label {\n        font-size: 0.875rem; // text-sm\n        color: #4a5568; // text-gray-700\n        font-weight: bold;\n        margin-bottom: 0.5em;\n    }\n\n    .react-aria-Group {\n        display: flex;\n        border: 1px solid #cbd5e0; // border border-gray-300\n        border-radius: 0.375rem; // rounded-md\n        background-color: #fff; // bg-white\n        padding-right: 0.5rem; // pr-2\n        // Group focus-within styles\n        &:focus-within {\n            border-color: #3b82f6; // border-blue-600\n            box-shadow: 0 0 0 1px #3b82f6; // ring-1 ring-blue-600\n        }\n    }\n\n    .react-aria-DateInput {\n        display: flex;\n        flex: 1; // flex-1\n        padding: 0.5rem; // p-2\n    }\n\n    .react-aria-DateSegment {\n        border-radius: 0.125rem; // rounded-sm\n        padding-left: 0.125rem; // px-0.5\n        padding-right: 0.125rem; // px-0.5\n        outline: none; // outline-none\n        &[data-focused] {\n            background-color: #bfdbfe; // focus:bg-blue-200\n            color: #1a202c; // focus:text-gray-900\n        }\n    }\n\n    .react-aria-Button {\n        margin-left: 0.5rem; // ml-2\n        padding: 0.5rem; // p-2\n        color: #4a5568; // text-gray-600\n        background: none;\n        border: none;\n        cursor: pointer;\n        &:hover {\n            color: #1a202c; // hover:text-gray-900\n        }\n\n        &--previous, &--next {\n            // styles specific to previous/next buttons if needed\n        }\n    }\n\n    .react-aria-Popover {\n        position: absolute;\n        z-index: 1000;\n        width: auto; // w-auto\n        border-radius: 0.375rem; // rounded-md\n        border: 1px solid #cbd5e0; // border border-gray-300\n        background-color: #fff; // bg-white\n        padding: 1rem; // p-4\n        box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05); // shadow-lg\n    }\n\n    .react-aria-Dialog {\n        // Dialog itself often doesn't need much direct styling if Popover handles container\n    }\n\n    .react-aria-Calendar {\n        // Calendar container styles\n        header.react-aria-CalendarHeader {\n            display: flex;\n            align-items: center; // items-center\n            justify-content: space-between; // justify-between\n            padding-bottom: 1rem; // pb-4\n\n            h2.react-aria-CalendarHeader--title {\n                font-size: 1.125rem; // text-lg\n                font-weight: 600; // font-semibold\n                color: #2d3748; // text-gray-800\n            }\n        }\n    }\n\n    .react-aria-CalendarGrid {\n        display: grid;\n        // grid-template-columns: repeat(7, minmax(0, 1fr)); // grid-cols-7\n        grid-template-columns: repeat(7, 1fr); // Simpler 1fr\n        gap: 0.5rem; // gap-2\n    }\n\n    .react-aria-CalendarHeaderCell {\n        font-size: 0.75rem; // text-xs\n        font-weight: 600; // font-semibold\n        color: #718096; // text-gray-500\n        text-align: center;\n        padding: 0.25rem 0; // py-1\n    }\n\n    .react-aria-CalendarCell {\n        display: flex; // flex\n        height: 2rem; // h-8\n        width: 2rem; // w-8\n        align-items: center; // items-center\n        justify-content: center; // justify-center\n        border-radius: 9999px; // rounded-full\n        font-size: 0.875rem; // text-sm\n        color: #4a5568; // text-gray-700\n        outline: none; // outline-none\n        cursor: pointer; // cursor-pointer\n\n        &[data-disabled] {\n            color: #a0aec0; // data-[disabled]:text-gray-400\n            cursor: not-allowed;\n        }\n        &[data-selected] {\n            background-color: #3b82f6; // data-[selected]:bg-blue-600\n            color: #fff; // data-[selected]:text-white\n        }\n        &[data-focused] {\n            box-shadow: 0 0 0 2px #60a5fa; // data-[focused]:ring-2 data-[focused]:ring-blue-400\n        }\n        &[data-today] {\n            font-weight: bold; // data-[today]:font-bold\n        }\n        &:hover:not([data-selected]):not([data-disabled]) {\n            background-color: #e2e8f0; // hover:bg-gray-200\n        }\n    }\n}\n"],"sourceRoot":""}]);
